import React, {useEffect, useRef, useState} from 'react'
import {
  CBadge,
  CButton,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CCollapse,
  CDataTable, CInvalidFeedback,
  CModal,
  CModalBody,
  CModalHeader,
  CRow,
  CSelect, CTextarea
} from '@coreui/react'
import {NotificationManager} from 'react-notifications';
import {sendToSales, loadComplaintByDateAndStatus, loadInspectionsByDateRange} from '../../services/inspectionClient';
import {listStatuses,listAssessmentbyId} from '../../services/client';


import {useStore} from "../../MobxStore";
import DialogTitle from "@material-ui/core/DialogTitle/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import Slide from "@material-ui/core/Slide/Slide";
import DatePicker from "react-datepicker";
import dateFormat from "dateformat";
import ImageGallery from 'react-image-gallery';
import "react-image-gallery/styles/css/image-gallery.css";

import {getRootUrl} from "../../services/rootUrl";
import {useReactToPrint} from 'react-to-print';
import {CheckListToPrint} from './CheckListToPrint';
import {CodeListToPrint} from "./CodeListToPrint";
import {TaskHistory} from "./TaskHistory";
import {AssessmentToPrint} from "./AssessmentToPrint";
import {PictureToPrint} from "./PictureToPrint";


const getBadge = status => {
  switch (status) {
    case 20:
      return 'danger';
    case 22:
      return 'success';
    case 21:
      return 'info';
    default:
      return 'primary'
  }
};

const priorityBadge = status => {
  switch (status) {
    case "High":
      return 'danger';
    case "Medium":
      return 'warning';
    case "Low":
      return 'primary';
    default:
      return 'primary'
  }
};

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const Inspections = () => {

    const componentRef = useRef();
    const handlePrint = useReactToPrint({
      content: () => componentRef.current,
    });
    const [checklistDialog, setChecklistDialog] = useState(false);
    const [assessmentDialog, setAssessmentDialog] = useState(false);
    const [codeListDialog, setCodeListDialog] = useState(false);
    const [taskHistoryDialog, setTaskHistoryDialog] = useState(false);
    const [pictureDialog, setPictureDialog] = useState(false);

  const openChecklistDialog=()=>{
      setChecklistDialog(true);
  }
  const closeChecklistDialog=()=>{
      setChecklistDialog(false);
  }

  const openCodeListDialog=()=>{
    console.log("--------------------- open code list")
    setCodeListDialog(true);
  }
  const closeCodeListDialog=()=>{
    setCodeListDialog(false);
  }

  const openAssessmentDialog=()=>{
    console.log("--------------------- open code list")
    setAssessmentDialog(true);
  }
  const closeAssessmentDialog=()=>{
    setAssessmentDialog(false);
  }
  const loadAssessmentById=(item)=>{
    listAssessmentbyId(store.login.token, item.id).then(response => {
      setAssessment(response)
      console.log(response)
      setTaskTobeAltered(item);
      openAssessmentDialog()
    })
      .catch(err => console.log(err));
  }


const openTaskHistoryDialog=()=>{
    console.log("--------------------- open code list")
    setTaskHistoryDialog(true);
  }
  const closeHistoryDialog=()=>{
    setTaskHistoryDialog(false);
  }
const openPictureDialog=()=>{
    console.log("--------------------- open code list")
    setPictureDialog(true);
  }
  const closePictureDialog=()=>{
    setPictureDialog(false);
  }

  const store = useStore();

  useEffect(() => {
    const fields = [];
    fields.push(
      {
        key: 'problemType',
        label: 'Problem Type',
        sorter: false,
        filter: false
      },{
        key: 'customerName',
        label: 'Customer Name',
        sorter: false,
        filter: false
      },{
        key: 'phoneNumber',
        label: 'Phone Number',
        sorter: false,
        filter: false
      },{
        key: 'metterNumber',
        label: 'Meter Number',
        sorter: false,
        filter: false
      },{
        key: 'status',
        label: 'Status',
        sorter: false,
        filter: false
      },{
        key: 'show_details',
        label: 'Details',
        sorter: false,
        filter: false
      }
      );

    setColumns(fields);
  }, []);


  const loadStatusList = () => {
    listStatuses(store.login.token).then(response => {
      setStatuses(response)
    })
      .catch(err => console.log(err));

  };

  const handleSendToSales = (taskId) => {
    if(selectedPriority==0){
      setPriorityInvalid(true)
    }else{

      console.log("Priority Selected Proceeding to submit.")
      sendToSales (store.login.token, taskId, selectedPriority, salesNote).then(response => {

        if(response.status){
          load_Inspections()
          setSelectedPriority(0);
          setSalesNote('');
          setDialogConfirm(false);
          NotificationManager.success(response.message, 'Success', 3000);
        }else{
          NotificationManager.error(response.message, 'Failed!', 3000);
        }


      })
        .catch(err => console.log(err));
    }


  };


  const [statuses, setStatuses] = useState([]);
  const [assessment, setAssessment] = useState([]);
  const [selectedStatus, setSelectedStatus] = useState('');
  const [selectedPriority, setSelectedPriority] = useState('');
  const [salesNote, setSalesNote] = useState('');
  const [columns, setColumns] = useState([]);
  const [inspections, setInspections] = useState([]);
  const [dialogConfirm, setDialogConfirm] = React.useState(false);
  const [taskTobeAltered, setTaskTobeAltered] = useState({});
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());



  const [priorityInvalid, setPriorityInvalid] = useState(false);



  const[images, setImages]= useState([]);
  const [filesModal, setFilesModal] = useState(false);

  const handleLoadComplaints=()=>{
    if(selectedStatus==0){
      load_Inspections()
    }else if(selectedStatus>0){
      load_dailyComplaintByStatus()
    }

  };


  const load_Inspections = () => {
    loadInspectionsByDateRange(store.login.token, dateFormat(startDate, "yyyy/mm/dd"), dateFormat(endDate, "yyyy/mm/dd")).then(response => {
      setInspections(response);
      console.log(response)
    })
      .catch(err => console.log(err));

  };


  const load_dailyComplaintByStatus = () => {

    loadComplaintByDateAndStatus(store.login.token, dateFormat(startDate, "yyyy/mm/dd"),dateFormat(endDate, "yyyy/mm/dd"),selectedStatus).then(response => {
      setInspections(response);
    })
      .catch(err => console.log(err));

  };

  // loadComplaints();

  useEffect(() => {
    load_Inspections();
    loadStatusList();
  }, []);



  const [details, setDetails] = useState([]);
  // const [items, setItems] = useState(usersData)

  const toggleDetails = (index) => {
    const position = details.indexOf(index);
    let newDetails = details.slice();
    if (position !== -1) {
      newDetails.splice(position, 1)
    } else {
      newDetails = [...details, index]
    }
    setDetails(newDetails)
  };

  const confirmDialog = (task) => {
    setDialogConfirm(true);
    setTaskTobeAltered(task);
  };


  const handleShowFiles=(item)=> {
    const imagesList = [];
    const rootUrl = getRootUrl().file_url;

    if (item.labFiles.length > 0) {

      item.labFiles.forEach(file => {
        imagesList.push(
          {
            original: rootUrl + file,
            thumbnail: rootUrl + file,
          });

        console.log(rootUrl + file);
      })
      setImages(imagesList)
      setFilesModal(true);
    }
  }


  return (
    <>

      <div>
        {checklistDialog?
        <CModal
          show={checklistDialog}
          onClose={closeChecklistDialog}
          size='xl'
          backdrop
          close
          closeOnBackdrop={false}
        >
          <CModalHeader size="modal-lg" closeButton/>
          <CheckListToPrint ref={componentRef} inspections={taskTobeAltered} />
          <CModalBody>
            <hr style={{marginTop:40}}/>
            <div className="d-flex justify-content-between">
              <button type="button" className="btn btn-outline-danger m-2 btn-sm" onClick={() => {
                closeChecklistDialog()
              }}>Close
              </button>

              <button className="btn btn-outline-success m-2 btn-sm" type="submit" onClick={(e) => {
                handlePrint()
              }}>Print this out!
              </button>

            </div>

          </CModalBody>

        </CModal>:<div></div>}
      </div>
      <div>
        {codeListDialog?
          <>
            {console.log("openening ")}
          <CModal
            show={codeListDialog}
            onClose={closeCodeListDialog}
            size='xl'
            backdrop
            close
            closeOnBackdrop={false}
          >
            <CModalHeader size="modal-lg" closeButton/>
            <CodeListToPrint ref={componentRef} inspections={taskTobeAltered} />
            <CModalBody>
              <hr style={{marginTop:40}}/>
              <div className="d-flex justify-content-between">
                <button type="button" className="btn btn-outline-danger m-2 btn-sm" onClick={() => {
                  closeCodeListDialog()
                }}>Close
                </button>

                <button className="btn btn-outline-success m-2 btn-sm" type="submit" onClick={(e) => {
                  handlePrint()
                }}>Print this out!
                </button>

              </div>

            </CModalBody>

          </CModal>
          </>:<div></div>}

      </div>
      <div>
        {assessmentDialog?
          <>
            {console.log("openening ")}
            <CModal
              show={assessmentDialog}
              onClose={closeAssessmentDialog}
              size='xl'
              backdrop
              close
              closeOnBackdrop={false}
            >
              <CModalHeader size="modal-lg" closeButton/>
              <AssessmentToPrint ref={componentRef} inspections={taskTobeAltered} assessment = {assessment[0]} />
              <CModalBody>
                <hr style={{marginTop:40}}/>
                <div className="d-flex justify-content-between">
                  <button type="button" className="btn btn-outline-danger m-2 btn-sm" onClick={() => {
                    closeCodeListDialog()
                  }}>Close
                  </button>

                  <button className="btn btn-outline-success m-2 btn-sm" type="submit" onClick={(e) => {
                    handlePrint()
                  }}>Print this out!
                  </button>

                </div>

              </CModalBody>

            </CModal>
          </>:<div></div>}

      </div>

      <div>
        {taskHistoryDialog?
          <>
            {console.log("openening ")}
            <CModal
              show={taskHistoryDialog}
              onClose={closeHistoryDialog}
              size='lg'
              backdrop
              close
              closeOnBackdrop={false}
            >
              <CModalHeader size="modal-lg" closeButton/>
              <TaskHistory ref={componentRef} task={taskTobeAltered} />
              <CModalBody>
                <hr style={{marginTop:40}}/>
                <div className="d-flex justify-content-end">
                  <button type="button" className="btn btn-outline-danger m-2 btn-sm" onClick={() => {
                    closeHistoryDialog()
                  }}>Close
                  </button>

                </div>

              </CModalBody>

            </CModal>
          </>:<div></div>}

      </div>
      <div>
        {pictureDialog?
          <>
            {console.log("openening ")}
            <CModal
              show={pictureDialog}
              onClose={closePictureDialog}
              size='xl'
              backdrop
              close
              closeOnBackdrop={false}
            >
              <CModalHeader size="modal-lg" closeButton/>
              <PictureToPrint ref={componentRef} inspections={taskTobeAltered} />
              <CModalBody>
                <hr style={{marginTop:40}}/>
                <div className="d-flex justify-content-end">
                  <button type="button" className="btn btn-outline-danger m-2 btn-sm" onClick={() => {
                    closePictureDialog()
                  }}>Close
                  </button>

                </div>

              </CModalBody>

            </CModal>
          </>:<div></div>}

      </div>
      <CRow>
        <CCol>
          <CCard>
            <CCardHeader>

              <div className="d-flex justify-content-between">
                <CRow>
                  <h4>Registered Inspections</h4>
                  {dateFormat(startDate, "yyyy/mm/dd")===dateFormat(endDate, "yyyy/mm/dd")?<div style={{marginLeft:40}}><strong>{dateFormat(startDate, "dddd, mmmm dS, yyyy")}</strong>

                    </div>:
                    <div style={{marginLeft:40}}>from &nbsp;&nbsp;&nbsp;<strong>{dateFormat(startDate, "dddd, mmmm dS, yyyy")}</strong>
                      &nbsp;&nbsp;&nbsp; to &nbsp;&nbsp;&nbsp;<strong>{dateFormat(endDate, "dddd, mmmm dS, yyyy")}</strong>
                    </div>}
                </CRow>

              </div>
              <div>

                <CRow>

                  <div style={{marginLeft:20}}>
                    <div>
                      Start Date
                    </div>
                    <DatePicker
                      selected={startDate}
                      onChange={date => {
                        setStartDate(date);
                        // setSelectedTeam(0);
                        // setSelectedStatus(0);

                      }
                      } />

                  </div>
                  <div style={{marginLeft:20}} >
                    <div>
                      End Date
                    </div>
                    <DatePicker
                      selected={endDate}
                      onChange={date => {
                        setEndDate(date);
                        // setSelectedTeam(0);
                        // setSelectedStatus(0);

                      }
                      } />

                  </div>
                  <CCol xs="6" md="3" size="sm">
                    <div>
                      Select Status
                    </div>
                    <CSelect
                      custom
                      name="selectLg"
                      id="selectLg"
                      // disabled={assignLater}
                      value={selectedStatus}
                      onChange={e => {
                        setSelectedStatus(e.target.value);
                      }}>
                      <option key={0} value={0}>-- All --</option>
                      {statuses.map((item, i) => {
                        return (
                          <option key={i} value={item.id}>{item.statusName}</option>
                        )
                      }, this)}

                    </CSelect>

                  </CCol>
                  <CButton className="btn btn-outline-success m-3 btn-sm"
                           onClick={()=>{
                             handleLoadComplaints();
                           }}
                           style={{paddingLeft:"200", paddingRight:"20"}}
                  >{"Load \t"}


                  </CButton>
                </CRow>
              </div>
            </CCardHeader>
            <CCardBody>

              <CDataTable
                items={inspections}
                fields={columns}
                // columnFilter
                tableFilter
                // footer
                itemsPerPageSelect
                itemsPerPage={20}
                hover
                size="sm"
                striped
                bordered
                outlined
                pagination
                scopedSlots={{
                  'status':
                    (item) => (
                      <td>
                        <CBadge color={getBadge(item.statusId)}>
                          {item.status}
                        </CBadge>
                      </td>
                    ),'show_details':
                    (item, index) => {
                      return (
                        <td className="py-2">
                          <CButton
                            color="primary"
                            variant="outline"
                            shape="square"
                            size="sm"
                            onClick={() => {
                              toggleDetails(index)
                            }}
                          >
                            {details.includes(index) ? 'Hide' : 'Show'}
                          </CButton>
                        </td>
                      )
                    },

                  'details':
                    (item, index) => {
                      return (
                        <CCollapse show={details.includes(index)}>
                          <div className="d-flex justify-content-end">
                            <button type="button" className="btn btn-outline-warning m-2 btn-sm" onClick={() => {
                              openTaskHistoryDialog()
                              setTaskTobeAltered(item);
                            }}>View History
                            </button>
                            <button type="button" className="btn btn-outline-info m-2 btn-sm" onClick={() => {
                              openChecklistDialog()
                              setTaskTobeAltered(item);
                            }}>View CheckList
                            </button>
                            {item.codeResultList!=null&&item.codeResultList.length>0?
                              <button type="button" className="btn btn-outline-info m-2 btn-sm" onClick={() => {
                              openCodeListDialog()
                              setTaskTobeAltered(item);
                            }}>View CodeList
                            </button>:""}
                            {item.assesmentSubmitted?
                              <button type="button" className="btn btn-outline-info m-2 btn-sm" onClick={() => {
                                loadAssessmentById(item)

                            }}>View Assesment
                            </button>:""}
                            {item.filePath!=""?
                              <button type="button" className="btn btn-outline-info m-2 btn-sm" onClick={() => {
                                openPictureDialog(item)
                                setTaskTobeAltered(item);
                            }}>View Picture
                            </button>:""}
                          </div>
                          <CCardBody>
                            <div className="d-flex justify-content-between">
                              <div style={{width: "50%"}}>
                                <div className="d-flex justify-content-start">
                                  <div style={{width: "40%",}}><h6 ><strong>Location</strong></h6></div>
                                  <div style={{marginRight: 4, width: "60%",}}><h6>
                                    {item.location}
                                  </h6></div>
                                </div>
                                <div className="d-flex justify-content-start">
                                  <div style={{width: "40%",}}><h6 ><strong>Registered By</strong></h6></div>
                                  <div style={{marginRight: 4, width: "60%",}}><h6>
                                    {item.registeredBy}
                                  </h6></div>
                                </div>
                                <div className="d-flex justify-content-start">
                                  <div style={{ width: "40%"}}><h6 ><strong>Registered
                                    On</strong></h6></div>
                                  <div style={{marginRight: 4,width: "60%"}}><h6>
                                    {new Date(parseFloat(item.registeredDate)).toLocaleString()}
                                  </h6></div>
                                </div>

                                { item.statusId == 11?<div className="d-flex justify-content-start">
                                  <div style={{ width: "40%"}}><h6 ><strong>Completed On
                                    On</strong></h6></div>
                                  <div style={{marginRight: 4,width: "60%"}}><h6>
                                    {new Date(parseFloat(item.completedDate)).toLocaleString()}
                                  </h6></div>
                                </div>:""}

                                { item.statusId ==12?<div className="d-flex justify-content-start">
                                  <div style={{ width: "40%"}}><h6 ><strong>Approved On
                                    On</strong></h6></div>
                                  <div style={{marginRight: 4,width: "60%"}}><h6>
                                    {new Date(parseFloat(item.approvedDate)).toLocaleString()}
                                  </h6></div>
                                </div>:""}

                              </div>
                              <div style={{width: "50%"}}>

                                <div>
                                  <div className="d-flex justify-content-start">
                                    <div style={{ width: "40%"}}><h6 ><strong>Tariff Category</strong></h6></div>
                                    <div style={{marginRight: 4, width: "60%"}}><h6>
                                      {
                                        item.tarifCat}
                                    </h6></div>
                                  </div>
                                  <div className="d-flex justify-content-start">
                                    <div style={{ width: "40%"}}><h6 ><strong>Connection Type</strong></h6></div>
                                    <div style={{marginRight: 4, width: "60%"}}><h6>
                                      {item.connType}
                                    </h6></div>
                                  </div><div className="d-flex justify-content-start">
                                    <div style={{ width: "40%"}}><h6 ><strong>Feeder</strong></h6></div>
                                    <div style={{marginRight: 4, width: "60%"}}><h6>
                                      {item.feeder}
                                    </h6></div>
                                  </div><div className="d-flex justify-content-start">
                                    <div style={{ width: "40%"}}><h6 ><strong>Tx No.</strong></h6></div>
                                    <div style={{marginRight: 4, width: "60%"}}><h6>
                                      {item.txNo}
                                    </h6></div>
                                  </div>
                                  <div className="d-flex justify-content-start">
                                    <div style={{ width: "40%"}}><h6 ><strong>Customer Status</strong></h6></div>
                                    <div style={{marginRight: 4, width: "60%"}}><h6>
                                      {item.customerStatus}
                                    </h6></div>
                                  </div>

                                </div>
                              </div>

                            </div>
                          </CCardBody>
                          <div className="d-flex justify-content-end">


                            {item.statusId==10?
                              <button type="button" className="btn btn-outline-warning m-2 btn-sm" onClick={() => {
                                confirmDialog(item)
                              }}>Send to Sales
                              </button>:""}
                          </div>
                        </CCollapse>
                      )
                    }
                }}
              />
            </CCardBody>
          </CCard>
        </CCol>

      </CRow>

      <CModal
        show={dialogConfirm}
        TransitionComponent={Transition}
        keepMounted
        // size='xl'
        aria-labelledby="alert-dialog-slide-title"
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle id="alert-dialog-slide-title">Send Inspection to Sales And Marketing</DialogTitle>
        <DialogContent>
          <DialogContentText >
            {<>
              <div style={{textAlign: 'left'}}><small>Customer Name:
                <strong>{" " + taskTobeAltered.customerName}</strong></small>
              </div>
              <div >
                <small>Problem Type
                  type <strong>{taskTobeAltered.problemType}</strong></small>
                <div className="d-flex justify-content-start" style={{marginTop:10}}>
                  <div style={{width:"30%"}}>Priority</div>
                  <div style={{alignContent:"left"}}>
                  <CSelect
                    custom
                    // disabled={assignLater}
                    value={selectedPriority}
                    invalid={priorityInvalid}
                    onChange={e => {
                      setSelectedPriority(e.target.value);
                      if (e.target.value != '') {
                        setPriorityInvalid(false)
                      } else {
                        setPriorityInvalid(true)
                      }
                    }}>
                    <option key={0} value={0}>-- Select Priority --</option>
                    <option key={1} value={1}>High</option>
                    <option key={2} value={2}>Medium</option>
                    <option key={3} value={3}>Low</option>

                  </CSelect>
                    <CInvalidFeedback className="help-block">
                      Please select a Priority Level
                    </CInvalidFeedback>
                  </div>
                </div>
                <div className="d-flex justify-content-start" style={{marginTop:10}}>
                  <div style={{width:"30%"}}>Note</div>

                    <CTextarea
                      id="textarea-input"
                      rows="3"
                      // invalid={noteInvalid}
                      placeholder="Inspection Note..."
                      value={salesNote}
                      name="salesNote"
                      onChange={(e) => {
                        setSalesNote(e.target.value);

                      }}

                    />

                </div>

              </div></>}
          </DialogContentText>

        </DialogContent>
        <div className="d-flex justify-content-between">
          <button
            type="button"
            className="btn btn-outline-danger m-2 btn-sm"
            onClick={() => {
              setDialogConfirm(false)
              setSelectedPriority(0)
            }}>Cancel
          </button>

          <button className="btn btn-outline-success m-2 btn-sm" type="submit" onClick={() => {
            handleSendToSales(taskTobeAltered.id)
          }}>Continue
          </button>

        </div>
      </CModal>

      {/*uploaded files*/}
      <CModal
        show={filesModal}
        onClose={() => {
          setFilesModal(false)
        }}
        size='lg'
        backdrop
        closeOnBackdrop={false}
      >
        <CModalHeader size="modal-lg" closeButton>
          <div className="d-flex justify-content-between">
            <div>
              {<h5>Uploaded Files </h5>}

            </div>

          </div>
        </CModalHeader>
        <CCardBody>
          <ImageGallery items={images}/>
        </CCardBody>
      </CModal>


      </>
  )
};



export default Inspections


