export function updateTask(token, updateDto) {
  const rootUrl = getRootUrl().um_url;
  return axios({
    method: 'put',
    url: rootUrl + "/meterLab/updateComplaint",
    headers: {
      Authorization: `Bearer ${token}`
    },
    data: updateDto
  }).then(response => response.data)
}

export const uploadFile = (token, data) => {


  const rootUrl = getRootUrl().um_url;
  return axios({
    method: 'post',
    url: rootUrl + "/meterLab/uploadFile",
    headers: {
      Authorization: `Bearer ${token}`
    },
    data: data
  }).then(response => response.data)
};

